@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
* {
    max-width: 100%;
    margin: auto;
    color: #2f2f2f;
    font-family: 'Josefin Sans', sans-serif;
}

body {
    background-image: -webkit-linear-gradient(left, #868f96 0%, #596164 100%);
    background-image: linear-gradient(to right, #868f96 0%, #596164 100%);
    overflow-x: hidden;
}

section {
    text-align: center;
    width: 85%;
}

h1,
h2,
h3 {
    margin-bottom: 10%;
    font-family: Arial, Helvetica, sans-serif;
}

p,
span {
    color: #7c7c7c;
}

hr {
    margin: 5% 0 5% 0;
}

a {
    text-decoration: none;
    display: inline-block;
}

button {
    display: block;
    padding: 10px;
    color: #f9f9f9;
    background-color: rgba(0, 0, 0, 0);
    border: 1px solid white;
    border-radius: 5px;
    -webkit-transition-duration: 1s;
            transition-duration: 1s;
}

button:hover {
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.2);
}

header {
    text-align: center;
    height: 600px;
}

header h1 {
    margin-bottom: 3%;
    color: #fff;
}

.name-container {
    position: relative;
    top: 50%;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    text-align: center;
}

.name-container span {
    display: block;
    color: #f9f9f9;
    margin-bottom: 2%;
}

.nav-container {
    display: none;
}

.card-container {
    background-color: #fff;
    padding-top: 10%;
    width: 90%;
    max-width: 1250px;
    border-radius: 30px;
}

#round-img {
    width: 100%;
    margin-bottom: 5%;
    padding: 3%;
    border-radius: 50%;
    border: 1px solid rgba(0, 0, 0, 0.2);
}

#reflection {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
    opacity: 0.03;
}

#skills-section {
    padding-top: 20%;
}

.skill-conatiner div {
    margin-bottom: 8%;
}

.skill-conatiner span {
    display: block;
}

#contributions {
    padding-top: 20%;
}

#projects-section {
    padding-top: 20%;
}

.tech {
    width: 15%;
    padding-bottom: 15%;
}

.project-details button {
    color: gray;
    border-color: gray;
    margin: 5% auto 5% auto;
}

#contact {
    background-color: #efefef;
    padding-top: 15%;
    padding-bottom: 15%;
    border-radius: 0 0 30px 30px;
}

#contact * {
    display: block;
}

#contact-content input {
    margin-bottom: 3%;
    width: 60%;
    border-radius: 5px;
    margin-bottom: 2%;
    text-align: center;
    outline: none;
}

#contact-content textarea {
    resize: none;
    width: 70%;
    height: 70px;
    margin-bottom: 5%;
    border-radius: 15px;
    resize: none;
    outline: none;
}

#contact button {
    color: rgba(0, 0, 0, 0.8);
    border: 1px solid rgba(0, 0, 0, 0.8);
    ;
    width: 60%;
    -webkit-transition-duration: 1s;
            transition-duration: 1s;
}

#contact button:hover {
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.2);
}

footer {
    padding-top: 10%;
}

footer p {
    margin-left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    color: white;
    margin-bottom: 5%;
    text-align: center;
}

footer a {
    border-radius: 50%;
    border: 1px solid white;
    padding: 5%;
}

.social-media {
    text-align: center;
    margin: 10% 0 10% 0;
}

.social-media a {
    border-radius: 50%;
    border: 1px solid #efefef;
    -webkit-transition-duration: 1s;
            transition-duration: 1s;
    margin: 5% 0 10% 0;
}

.social-media a:hover {
    background-color: rgba(255, 255, 255, .2);
}

.mid-icon {
    margin: 0 5% 0 5%;
}

@media (min-width: 481px) {
    .card-container {
        padding: 0;
    }
    .nav-container {
        display: block;
        position: relative;
        z-index: 5;
        width: 100%;
    }
    #main-nav {
        position: absolute;
        top: 0;
        width: 100%;
        text-align: center;
        background-color: #efefef;
        border-radius: 30px 30px 0 0;
    }
    #main-nav p {
        position: relative;
        display: inline-block;
        color: #636363;
        padding: 3%;
        -webkit-transition-duration: 1s;
                transition-duration: 1s;
    }
    #main-nav p:hover {
        cursor: pointer;
        background-color: #fff;
    }
    .sticky-nav {
        position: fixed;
        top: 0;
        width: inherit;
        border-radius: 0px;
        will-change: transform;
    }
    #about-section {
        width: 90%;
        display: grid;
        grid-auto-flow: column dense;
        padding: 20% 0 10% 0;
        margin-bottom: 10%;
        text-align: left;
    }
    #about-section h3 {
        margin-bottom: 0;
        padding-bottom: 0;
    }
    hr {
        width: 30%;
        margin: 3% 0 3% 0;
    }
    #round-img {
        width: 80%;
    }
    #skills-section {
        text-align: center;
        padding-top: 5%;
        padding-bottom: 10%;
    }
    .skill-conatiner {
        display: flex;
    }
    #skill-card {
        width: 31%;
    }
    .skill-conatiner span {
        display: block;
        margin-bottom: 5%;
    }
    #skill-card * {
        position: relative;
        top: 30%;
    }
    #projects-section {
        padding-top: 10%;
    }
    .project-left {
        display: flex;
        padding-bottom: 10%;
    }
    .project-left div {
        width: 50%;
    }
    .project-right {
        padding-bottom: 10%;
        display: flex;
        flex-direction: row-reverse;
    }
    .project-right div {
        width: 50%;
    }
    .tech {
        margin-top: 5%;
        width: 50px;
        height: 50px;
    }
    .project-details {
        display: block;
    }
    a {
        margin-top: 2%;
        text-decoration: none;
    }
    .project-details button {
        width: 100%;
        color: #595959;
        border: 1px solid #7c7c7c;
    }
    .project-details button:hover {
        background-color: rgba(0, 0, 0, 0.1);
    }
    .social-media a {
        padding: 2%;
    }
}

@media (min-width: 1024px) {
    .nav-container {
        position: relative;
        z-index: 5;
        width: 100%;
    }
    .sticky-nav {
        position: fixed;
        max-width: 1250px;
        top: 0;
        width: inherit;
    }
    #about-section {
        margin-bottom: 0;
    }
    #skills-section {
        padding-top: 0%;
    }
    .skill-conatiner {
        display: grid;
        grid-template-columns: 33% 33% 33%;
    }
    #skill-card {
        width: auto;
        width: initial;
    }
    .skill-conatiner span {
        margin-left: 2%;
        display: block;
    }
    #contributions {
        padding-top: 5%;
    }
    .website-img {
        width: 500px;
        height: 250px;
    }
    .mobile-app-img {
        width: 300px;
        height: 350px;
    }
    #projects-section {
        padding-top: 1%;
    }
    #contact {
        padding: 5% 0 5% 0;
    }
    #contact-content input {
        display: block;
        width: 30%;
        border-radius: 5px;
        margin-bottom: 2%;
        text-align: center;
        outline: none;
    }
    #contact-content textarea {
        width: 35%;
        border-radius: 15px;
        resize: none;
        height: 100px;
        margin-bottom: 1%;
        outline: none;
    }
    #contact button {
        width: 20%;
    }
    .social-media {
        margin: 0;
        padding: 0;
    }
    .social-media a {
        padding: 1%;
    }
    .mid-icon {
        margin: auto 2% auto 2%;
    }
}
